* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  display: flex;
}

.Nav {
  width: 20%;
  height: 100vh;
  border-right: groove rgb(253, 251, 251) 0.1px;
  background: rgb(243, 239, 239);
  box-shadow: 4px 3px 8px 1px rgb(253, 250, 250);
  padding: 35px;
}

.Nav div {
  justify-content: space-between;
  justify-content: center;
  position: relative;
  padding: 15px;
  margin: 20px 20;
}

.Nav div a {
  text-decoration: none;
  color: black;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.Nav .logo {
  width: 80%;
  padding: 15px;
  margin: 10px;
}
.Main {
  width: 80%;
  background-color: rgb(243, 239, 239);
}

.search {
  position: relative;
  display: flex;
  background: white;
  padding: 5px;
  align-items: center;
  width: 70%;
  border-radius: 5px;
  justify-content: space-between;
  margin: 15px;
}

.search input {
  border: none;
  width: 80%;
  padding: 10px;
  // height: 100%;
}

.search input:focus {
  outline: none;
}

.search button {
  width: 15%;
  padding: 10px;
  background: blue;
  border-radius: 5px;
  border: none;
  color: white;
}

.Card {
  border-radius: 10px;
  // border: groove;
  width: 30%;
  // padding: 10px;
  margin: 10px;
  // height:10px;
  position:relative;
  box-shadow: 0 5px 10px rgba(154,160,185,.05), 0 15px 40px rgba(166,173,201,.2);
}
.filter {
  display: inline-flex;
  padding: 10px;
  border-radius: 10px;
  margin: 10px;
}

.filter .dropdown {
  padding: 5px 15px 5px 15px
}

.Img {
  // max-width: 30;
  // max-height: 80%;
  // display: block;
}

.Img_holder {
  border-radius: 10px;
  width: 100%;
}

.Button1 {
  background: blue;
  border-radius: 5px;
  padding: 5px;
  color: white;
  margin: 10px;
}

.Button2 {
  background: red;
  border-radius: 5px;
  padding: 5px;
  color: white;
  margin: 10px;
}

