.card_info {
  bottom: 0%;
  position:absolute;
  color:white;
  padding:5px;
  }
  
  .card-button {
  display: none;
  }
  
  .Card:hover {
   .card_info {
  background: #ffffff;
  width:100%;
  color:black;
  
  }
  .card-button {
  display: block;
  }
}



@media screen and (max-width: 500px) {

  .Card {
  width: 100%;
  }
  }
